<template>
  <div class="verfify-tenant-list-warpper">
    <app-header title="等待审核"> </app-header>
    <div class="blank"></div>

    <div class="tenant-list-content">
      <tenant-item
        v-for="item in verifyList"
        :key="item.code"
        :name="item.tenantName"
        @click="openVerifyPage(item)"
      >
        <div
          :class="{
            'wait-pending-text': item.approveStatus === allStatus.waiting,
            'reject-text': item.approveStatus === allStatus.reject,
          }"
        >
          {{ statusText[item.approveStatus] }}
        </div>
      </tenant-item>
    </div>
  </div>
</template>
<script>
import tenantItem from '@src/widget/tenant-list/tenant-item';
import TenantService from '@services/tenant-service';
import SsoCenterService from '@services/sso-center-service';
import UserService from '@services/user-service';
import STATIC from '@config/static';

let lastToken = '';

export default {
  components: {
    tenantItem,
  },

  data() {
    return {
      verifyList: [],
      allStatus: STATIC.SSO_APPROVE_STATUS,
      statusText: STATIC.SSO_APPROVE_STATUS_TEXT,
    };
  },

  activated() {
    this.init();
  },

  methods: {
    init() {
      lastToken = '';
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        overlay: true,
        forbidClick: true, // 禁用背景点击
        loadingType: 'spinner',
        message: '加载中...',
      });
      return TenantService.getTenantList()
        .then(list => {
          this.$toast.clear();
          console.log('list =====> ', list);
          this.verifyList = list.verifyTenantList || [];
        })
        .catch(() => {
          this.$toast.clear();
          this.verifyList = [];
        });
    },

    openVerifyPage(item) {
      let promise = Promise.resolve();

      // 如果有token，则通过token换取token
      if (lastToken) {
        promise = UserService.token2token(item.tenantCode, false, lastToken);
      } else {
        promise = UserService.ticket2token(item.tenantCode, false);
      }
      // 获取租户token
      return promise.then(res => {
        // 保存最后的token，防止ticket过期无法使用
        lastToken = res.accessToken || res;
        SsoCenterService.openPage('/auth/auditing', {
          token: lastToken,
          tenantCode: item.tenantCode,
          tenantName: item.tenantName,
          showLeftBtn: 1,
        });
      });
    },
  },
};
</script>
